import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  Button,
} from '@material-ui/core';
import { GrammageModal } from './GrammageModal';
import { useManageFoodState } from '../hooks/useManageFoodState.js.js';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import TuneIcon from '@material-ui/icons/Tune';

const useStyles = makeStyles((theme) => ({
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
    width: 151,
    backgroundSize: 'contain',
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  marginRight: {
    marginRight: '0.5rem',
  },
}));

export function RecipeCard({
  foodId,
  title,
  category,
  grammage,
  grammageInfo,
  imgSrc,
  variants,
}) {
  const classes = useStyles();
  const { removeFoodFromState } = useManageFoodState(title);

  const getPlaceholderUnit = (unit) => {
    if (unit === 'bigPiece') return `x ${grammageInfo.piece.bigPiece}g`;
    if (unit === 'smallPiece') return `x ${grammageInfo.piece.smallPiece}g`;
    if (unit === 'bigPackage')
      return `x ${grammageInfo.package.bigPackage}${grammageInfo.defaultUnit}`;
    if (unit === 'smallPackage')
      return `x ${grammageInfo.package.smallPackage}${grammageInfo.defaultUnit}`;
    if (unit === 'cup') return 'x 250ml';
    if (unit === 'smallCup') return 'x 200ml';
    if (unit === 'tbs') return 'x 15ml';
    if (unit === 'ts') return 'x 5ml';
    if (unit === 'pinch') return 'x 0.35g';
    return unit;
  };

  return (
    <Card className='recipe-card'>
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Typography component='h6' variant='h6'>
            {title}
          </Typography>
          <Typography
            className='grammage-text'
            component='h6'
            variant='h6'
            color='textSecondary'
          >
            {`${grammage.value} ${getPlaceholderUnit(grammage.unit)}`}
          </Typography>
          {/* <Typography variant='subtitle1' color='textSecondary'>
            {category}
          </Typography> */}
        </CardContent>
        <div className={classes.controls}>
          <GrammageModal
            title={title}
            grammageInfo={grammageInfo}
            currentUnit={grammage.unit}
            getPlaceholderUnit={getPlaceholderUnit}
            variants={variants}
            foodId={foodId}
            isSeasoning={false}
          >
            <Button
              className={classes.marginRight}
              variant='contained'
              color='primary'
            >
              <TuneIcon />
            </Button>
          </GrammageModal>
          <Button
            variant='outlined'
            color='primary'
            onClick={removeFoodFromState}
          >
            <DeleteForeverIcon />
          </Button>
        </div>
      </div>
      <CardMedia className={classes.cover} image={imgSrc} title='' />
    </Card>
  );
}
