import './App.scss';
import React, { useState, useRef, useEffect } from 'react';
import { useAuthValue } from './context/AuthContext';
import { useLocation } from 'react-router-dom';
import { Switch, Route, Redirect } from 'react-router-dom';
import { firebase } from './firebase';
import { useHistory } from 'react-router-dom';
import {
  RecipePage,
  MenuPage,
  ProfilePage,
  IntroPage,
  LoginPage,
  RegisterPage,
} from './pages/main';
import { Header, BottomNav } from './compontents/layout';
import { PrivateRoute, AlertBanner } from './compontents';
import { useUserDataValue } from './context/UserDataContext';

function App() {
  const [hideNav, setHideNav] = useState();
  const header = useRef();
  const { currentUser } = useAuthValue();
  const location = useLocation();
  const { userData, setUserData } = useUserDataValue();
  const history = useHistory();

  const getUserData = async () => {
    await firebase
      .firestore()
      .collection('userData')
      .where('userId', '==', currentUser && currentUser.uid)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          if (userData) {
            return;
          }
          setUserData(doc.data());
        });
      })
      .catch((error) => {
        console.log('Error getting documents: ', error);
      });
  };

  useEffect(() => {
    if (location.pathname === '/login' || location.pathname === '/register') {
      setHideNav(true);
      console.log('hidden');
    } else {
      setHideNav(false);
    }

    if (location.pathname === '/') {
      history.push('/create-recipe');
    }

    getUserData();
  });

  return (
    <Switch>
      <div className='app'>
        <AlertBanner />
        <Header passRef={header} />
        <div className='app__content'>
          {/* <PrivateRoute exact path='/' component={IntroPage} /> */}
          {currentUser && (
            <>
              {/* <Route path='/'>
                <Redirect to='/create-recipe' />
              </Route> */}
              <Route path='/my-profile'>
                <ProfilePage />
              </Route>
              <Route path='/create-recipe'>
                <RecipePage header={header} />
              </Route>
              <Route path='/my-recipes'>
                <MenuPage header={header} />
              </Route>
            </>
          )}
        </div>
        <BottomNav hideNav={hideNav} />

        {/* <Route path='/register'>
          <RegisterPage />
        </Route> */}
        <Route path='/login'>
          <LoginPage />
        </Route>
        <Route path='/register'>
          <RegisterPage />
        </Route>
        <Route path='/create-user'>
          <IntroPage />
        </Route>
      </div>
    </Switch>
  );
}

export default App;
