import React, { useState, useEffect } from 'react';
import { useFoodStateValue } from '../context/FoodStateContext';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers.js';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputLabel,
  Input,
  FormControl,
  TextField,
  Select,
  InputAdornment,
} from '@material-ui/core';
import clsx from 'clsx';
import { FOOD_LIST } from '../constants/food-list';
import { SEASONING_LIST } from '../constants/mock-seasoning';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  marginTop: {
    marginTop: '2rem',
  },
}));

export function GrammageModal({
  children,
  title,
  content,
  currentUnit,
  grammageInfo,
  variants,
  foodId,
  isSeasoning,
}) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [grammage, setGrammage] = useState('');
  const [grammageUnit, setGrammageUnit] = useState(
    grammageInfo.type === 'foodstuff' ? 'g' : 'ml'
  );
  const [foodVariant, setFoodVariant] = useState(0);

  const { setFoodState } = useFoodStateValue();
  const { getAvailableFood, getAvailableFoodIndex } = useFoodStateHelpers(
    title
  );

  const getGrammageStateValue = () => {
    return getAvailableFood(title).grammage.value;
  };

  const getSelectedVariant = () => {
    return getAvailableFood(title).selectedVariant;
  };

  const handleClose = () => {
    setOpen(false);
  };

  const getNutritionVariant = () => {
    let result;

    const foodNutrition = `nutrition${foodVariant === 0 ? '' : foodVariant}`;
    const foodPrice = `price${foodVariant === 0 ? '' : foodVariant}`;

    (isSeasoning ? SEASONING_LIST : FOOD_LIST).some(function (food) {
      if (food.id === foodId) {
        console.log(foodNutrition);
        result = { nutrition: food[foodNutrition], price: food[foodPrice] };
        return true;
      }
      return false;
    });

    return result;
  };

  //!
  const convertUnitValue = (unit, value, isMain = false) => {
    let convertedValue = Number(value);

    switch (unit) {
      case 'kg':
        convertedValue = value * 1000;
        break;
      case 'l':
        convertedValue = value * 1000;
        break;
      case 'bigPiece':
        if (grammageInfo.piece.bigPiece) {
          convertedValue = value * grammageInfo.piece.bigPiece;
        }
        break;
      case 'smallPiece':
        convertedValue = value * grammageInfo.piece.smallPiece;
        break;
      case 'bigPackage':
        if (grammageInfo.package.bigPackage) {
          convertedValue = value * grammageInfo.package.bigPackage;
        }
        break;
      case 'smallPackage':
        convertedValue = value * grammageInfo.package.smallPackage;
        break;
      case 'cup':
        convertedValue = value * 250;
        break;
      case 'smallCup':
        convertedValue = value * 200;
        break;
      case 'tbs':
        convertedValue = value * 15;
        break;
      case 'ts':
        convertedValue = value * 5;
        break;
      case 'pinch':
        convertedValue = value * 0.35;
        break;
      default:
    }

    if (isMain) return convertedValue / 1000;

    return convertedValue;
  };

  const getPlaceholderUnit = (unit) => {
    if (unit === 'g' || unit === 'ml' || unit === 'kg' || unit === 'l')
      return unit;
    return 'x';
  };

  const setGrammageState = () => {
    setFoodState((prevState) => {
      return prevState.map((food, index) => {
        if (index === getAvailableFoodIndex(title)) {
          return {
            ...prevState[index],
            nutrition: getNutritionVariant().nutrition,
            selectedVariant: foodVariant,
            grammage: {
              ...prevState[index].grammage,
              value: Number(grammage),
              //! check for milk 160ml
              valueInMainUnit: convertUnitValue(grammageUnit, grammage, true),
              valueInDefaultUnit: convertUnitValue(grammageUnit, grammage),
              unit: grammageUnit,
            },
            price: getNutritionVariant().price,
          };
        } else {
          return food;
        }
      });
    });

    handleClose();
  };

  useEffect(() => {
    getGrammageStateValue() !== 0 && setGrammage(getGrammageStateValue());

    setGrammageUnit(currentUnit);
    setFoodVariant(
      variants.length > 0 && getSelectedVariant() ? getSelectedVariant() : 0
    );
  }, []);

  return (
    <div className='grammage-modal'>
      <div onClick={() => setOpen(true)}>{children}</div>

      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={() => setOpen(false)}
      >
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>
          <form className={classes.container}>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor='demo-dialog-native'>Jednotka</InputLabel>
              <Select
                native
                value={grammageUnit}
                onChange={(e) => {
                  setGrammageUnit(e.target.value || '');
                  getNutritionVariant();
                }}
                input={<Input id='demo-dialog-native' />}
              >
                {grammageInfo.type === 'foodstuff' ||
                grammageInfo.type === 'ingredient' ? (
                  <>
                    <option value='kg'>Kilogramy</option>
                    <option value='g'>Gramy</option>
                    {grammageInfo.piece && grammageInfo.piece.bigPiece && (
                      <option value='bigPiece'>
                        Kus ({grammageInfo.piece.bigPiece}g)
                      </option>
                    )}
                    {grammageInfo.piece && grammageInfo.piece.smallPiece && (
                      <option value='smallPiece'>
                        Kus ({grammageInfo.piece.smallPiece}g)
                      </option>
                    )}
                    {grammageInfo.package && grammageInfo.package.bigPackage && (
                      <option value='bigPackage'>
                        Balen ({grammageInfo.package.bigPackage}
                        {grammageInfo.defaultUnit})
                      </option>
                    )}
                    {grammageInfo.package && grammageInfo.package.smallPackage && (
                      <option value='smallPackage'>
                        Balen ({grammageInfo.package.smallPackage}
                        {grammageInfo.defaultUnit})
                      </option>
                    )}
                  </>
                ) : grammageInfo.type === 'liquid' ||
                  grammageInfo.type === 'sauce' ? (
                  <>
                    <option value='l'>Litry</option>
                    <option value='ml'>Mililitry</option>
                    <option value='cup'>Sklenice (250ml)</option>
                    <option value='smallCup'>Mal Sklenice (200ml)</option>
                    <option value='tbs'>Lce (15ml)</option>
                    <option value='ts'>Lika (5ml)</option>
                    {grammageInfo.package && grammageInfo.package.bigPackage && (
                      <option value='bigPackage'>
                        Balen {grammageInfo.package.bigPackage}
                        {grammageInfo.defaultUnit}
                      </option>
                    )}
                    {grammageInfo.package && grammageInfo.package.smallPackage && (
                      <option value='smallPackage'>
                        Balen ({grammageInfo.package.smallPackage}
                        {grammageInfo.defaultUnit})
                      </option>
                    )}
                  </>
                ) : grammageInfo.type === 'seasoning' ? (
                  <>
                    <option value='g'>Gramy</option>
                    <option value='pinch'>petka (0.35g)</option>
                    <option value='tbs'>Lce (15g)</option>
                    <option value='ts'>Lika (5g)</option>
                    {grammageInfo.package && grammageInfo.package.bigPackage && (
                      <option value='bigPackage'>
                        Balen {grammageInfo.package.bigPackage}
                        {grammageInfo.defaultUnit}
                      </option>
                    )}
                    {grammageInfo.package && grammageInfo.package.smallPackage && (
                      <option value='smallPackage'>
                        Balen ({grammageInfo.package.smallPackage}
                        {grammageInfo.defaultUnit})
                      </option>
                    )}
                  </>
                ) : (
                  ''
                )}
              </Select>
            </FormControl>
            {variants.length > 0 && (
              <FormControl className={classes.formControl}>
                <InputLabel htmlFor='demo-dialog-native'>Varianta</InputLabel>
                <Select
                  native
                  value={foodVariant}
                  onChange={(e) => {
                    setFoodVariant(Number(e.target.value) || 0);
                    console.log(foodVariant);
                  }}
                  input={<Input id='demo-dialog-native' />}
                >
                  {variants.map((variant, i) => (
                    <option value={i}>{variant}</option>
                  ))}
                </Select>
              </FormControl>
            )}

            <TextField
              type='number'
              onInput={(e) => {
                setGrammage(e.target.value);
              }}
              label='Zadejte Hmotnost'
              id='outlined-start-adornment'
              className={clsx(classes.marginTop, classes.textField)}
              InputProps={{
                startAdornment: (
                  //!redo the unit system
                  //!milk shows as 1000g instead od ml
                  <InputAdornment position='start'>
                    {getPlaceholderUnit(grammageUnit)}
                  </InputAdornment>
                ),
              }}
              value={grammage}
              variant='outlined'
            />
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color='primary'>
            Zruit
          </Button>
          <Button onClick={setGrammageState} color='primary'>
            Potvrdit
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
