import React, { useEffect, useState } from 'react';
import {
  Button,
  Card,
  CardActions,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import { useManageFoodState, useFoodStateHelpers } from '../hooks';

const useStyles = makeStyles((theme) => ({
  checkMark: {
    width: '1.3rem',
    height: '1.3rem',
    color: '#1976d2',
    marginLeft: '1rem',
  },
  fitImage: {
    objectFit: 'contain',
  },
}));

export const RecipeFoodCard = ({
  foodId,
  title,
  imgSrc,
  nutrition,
  grammageInfo,
  price,
  variants,
  nutrientValue,
  nutrientUnit,
  category,
}) => {
  const { isFoodAvailable } = useFoodStateHelpers();
  const { addFoodToState, removeFoodFromState } = useManageFoodState(
    title,
    imgSrc,
    nutrition,
    foodId,
    grammageInfo,
    price,
    variants,
    category
  );

  const [included, setIncluded] = useState(false);
  const classes = useStyles();

  const handeFoodInclude = () => {
    if (!included) {
      setIncluded(true);
      addFoodToState();
    } else {
      setIncluded(false);
      removeFoodFromState();
    }
  };

  useEffect(() => {
    isFoodAvailable(title) ? setIncluded(true) : setIncluded(false);
  }, [isFoodAvailable, title]);

  const loweredCamelTitle = title.replace(/\s/g, '-').toLowerCase();

  return (
    <Card className={`food-card food-card--${loweredCamelTitle}`}>
      <CardActionArea onClick={() => handeFoodInclude()}>
        <CardMedia
          component='img'
          alt='Contemplative Reptile'
          height='140'
          image={imgSrc}
          title={title}
          className={classes.fitImage}
        />
        <CardContent className='food-card__content'>
          <div>
            <Typography variant='h6' component='h3'>
              {title}
            </Typography>
            <Typography variant='h6' component='h3' color='textSecondary'>
              {nutrientValue} {nutrientUnit}
            </Typography>
          </div>
          {included && <CheckCircleIcon className={classes.checkMark} />}
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button variant='outlined' size='small' color='primary'>
          Info
        </Button>
        <Button
          size='small'
          // variant={included ? 'contained' : 'outlined'}
          variant='contained'
          color='primary'
          onClick={() => handeFoodInclude()}
        >
          {included ? 'Odebrat' : 'Pidat'}
        </Button>
      </CardActions>
    </Card>
  );
};
