import React, { useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import { LottieAnimation } from '../../compontents/LottieAnimation';
import { Step3 } from '../Step3';
import { Step2 } from '../Step2';
import { Step1 } from '../Step1';
import { useHistory } from 'react-router-dom';
import { useAuthValue } from '../../context/AuthContext';
import { firebase } from '../../firebase';

import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Typography from '@material-ui/core/Typography';
import { useResponseStateValue } from '../../context/ResponseStateContext';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
  marginBottom: {
    marginBottom: '1rem',
  },
}));

function getSteps() {
  return ['Osobn daje', 'Biometriky', 'Aktivita'];
}

export const IntroPage = () => {
  const history = useHistory();
  const [complete, setComplete] = useState(false);

  const classes = useStyles();
  const [activeStep, setActiveStep] = useState(0);
  const steps = getSteps();
  const { setResponse } = useResponseStateValue();

  const { currentUser } = useAuthValue();

  const handleStep = (step) => {
    setActiveStep(step);
  };

  const [username, setUsername] = useState('');
  const [age, setAge] = useState();
  const [height, setHeight] = useState();
  const [weight, setWeight] = useState();
  const [gender, setGender] = useState();
  const [activityRatio, setActivityRatio] = useState();
  const [workActivityRatio, setWorkActivityRatio] = useState();

  const hasUserData = async () => {
    let hasData = false;
    await firebase
      .firestore()
      .collection('userData')
      .where('userId', '==', currentUser.uid)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          console.log(doc.data());
          if (doc.data()) {
            hasData = true;
          }
        });
      })
      .catch((error) => {
        console.log('Error getting documents: ', error);
      });

    return hasData;
  };

  const calculatePalRatio = () => {
    const parsedActivity = Number(activityRatio);
    const parsedWorkActivity = Number(workActivityRatio);

    let finalValue = (parsedActivity + parsedWorkActivity) / 2;

    return finalValue;
  };

  const createUserData = async () => {
    try {
      await firebase
        .firestore()
        .collection('userData')
        .add({
          userId: currentUser && currentUser.uid,
          username,
          age,
          height,
          weight,
          gender,
          palRatio: calculatePalRatio(),
        });
    } catch (error) {
      setResponse({
        active: true,
        message: error.message,
        type: 'error',
      });
    }
  };

  useEffect(() => {
    if (currentUser) {
      hasUserData().then((data) => {
        if (data) {
          history.push('/create-recipe');
        }
      });
    }
  }, []);

  return (
    <div
      // className={`auth intro-page ${hidden ? 'intro-page--hidden' : undefined}`}
      className={`auth intro-page ${
        complete ? 'intro-page--complete' : undefined
      }`}
    >
      <div className={`${classes.root} intro-page__steps`}>
        <Typography
          style={{ padding: '24px', paddingBottom: '10px' }}
          variant='h4'
          component='h3'
        >
          Tvorba tu
        </Typography>
        <Stepper activeStep={activeStep} orientation='vertical'>
          {steps.map((label, index) => (
            <Step key={label}>
              <StepLabel style={{ marginBottom: '2rem' }}>{label}</StepLabel>
              <StepContent>
                {/* <Typography className={classes.marginBottom}>
                  {getStepContent(index)}
                </Typography> */}
                <div className='intro-page__content'>
                  {index === 0 ? (
                    <Step1
                      setUsername={setUsername}
                      username={username}
                      handleStep={handleStep}
                    />
                  ) : index === 1 ? (
                    <Step2
                      age={age}
                      setAge={setAge}
                      height={height}
                      setHeight={setHeight}
                      setWeight={setWeight}
                      weight={weight}
                      gender={gender}
                      setGender={setGender}
                      handleStep={handleStep}
                    />
                  ) : index === 2 ? (
                    <Step3
                      complete={complete}
                      setComplete={setComplete}
                      activityRatio={activityRatio}
                      setActivityRatio={setActivityRatio}
                      setWorkActivityRatio={setWorkActivityRatio}
                      handleStep={handleStep}
                      createUserData={createUserData}
                      calculatePalRatio={calculatePalRatio}
                    />
                  ) : (
                    ''
                  )}
                </div>
              </StepContent>
            </Step>
          ))}
        </Stepper>
        {/* {activeStep === steps.length && (
          <Paper square elevation={0} className={classes.resetContainer}>
            <Typography>All steps completed - you&apos;re finished</Typography>
            <Button onClick={handleReset} className={classes.button}>
              Reset
            </Button>
          </Paper>
        )} */}
      </div>
      <div className='intro-page__welcome'>
        <LottieAnimation url='./images/animations/heart-pop.json' />
        <h2 className='intro-page__title'>Vtej Na Cholify!</h2>
        <Button
          className='intro-page__finish'
          variant='contained'
          color='primary'
          onClick={() => history.push('/create-recipe')}
        >
          Vytvoit Recept
        </Button>
      </div>
    </div>
  );
};
